import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {MessagesService} from '../messages.service';
import {ButtonsService} from '../buttons.service';

@Component({
  selector: 'app-message-add',
  templateUrl: './message-add.component.html',
  styleUrls: ['./message-add.component.css']
})
export class MessageAddComponent implements OnInit {

  id: any;
  message: any = {
    message: '',
    description: '',
    next: null,
    type: null,
    input: false
  };
  messages: any;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private messagesService: MessagesService,
              private buttonsService: ButtonsService) {
    if (this.route.snapshot.params.id) {
      this.id = this.route.snapshot.params.id;
      this.getOneMessage(this.id);
    }
  }

  ngOnInit() {
    this.getAllMessages();
  }

  getAllMessages() {
    this.messagesService.getAllQuestions().subscribe((res: any) => {
      this.messages = res;
    });
  }

  getOneMessage(id) {
    this.messagesService.getOneQuestion(id).subscribe((res: any) => {
      this.message = res;
    });
  }

  addUpdateMessage() {
    if (this.id) {
      this.messagesService.updateQuestion(this.message).subscribe((res: any) => {
        alert('Updated ');
      });
    } else {
      this.messagesService.addQuestion(this.message).subscribe((res: any) => {
        console.log(res);
        alert('Added');
      });
    }
  }


}



