import {ModuleWithProviders} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

import {ButtonsComponent} from './buttons/buttons.component';
import {ButtonsAddComponent} from './buttons-add/buttons-add.component';
import {MessagesListComponent} from './messages-list/messages-list.component';
import {MessageAddComponent} from './message-add/message-add.component';

export const router: Routes = [
  {path: '', component: MessagesListComponent},
  {path: 'messages-add', component: MessageAddComponent},
  {path: 'messages-update/:id', component: MessageAddComponent},
  {path: 'buttons', component: ButtonsComponent},
  {path: 'buttons-update/:id', component: ButtonsAddComponent},
  {path: 'buttons-add', component: ButtonsAddComponent},
];

export const routes: ModuleWithProviders = RouterModule.forRoot(router);



