import {Component, OnInit} from '@angular/core';
import {ButtonsService} from '../buttons.service';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-buttons-add',
  templateUrl: './buttons-add.component.html',
  styleUrls: ['./buttons-add.component.css']
})
export class ButtonsAddComponent implements OnInit {

  public button = {
    buttons: [],
    message: ''
  };

  id: any;

  constructor(public buttonsService: ButtonsService,
              private route: ActivatedRoute) {
    if (this.route.snapshot.params.id) {
      this.id = this.route.snapshot.params.id;
      this.getOneButton();
    }
  }

  ngOnInit() {
  }

  addButton() {
    if (this.button.buttons.length < 9) {
      this.button.buttons.push({
        ID: this.button.buttons.length + 1,
        next: null,
        type: null,
        name: ''
      });
    } else {
      alert('FB allows only 9 buttons');
    }

  }

  saveButton() {

    if (this.id) {
      console.log(this.button);
      this.buttonsService.updateButton(this.button).subscribe((saved) => {
        alert('updated');
      });
    } else {
      console.log(this.button);
      this.buttonsService.addButton(this.button).subscribe((saved) => {
        alert('saved');
      });
    }
  }

  getOneButton() {
    this.buttonsService.getOneButton(this.id).subscribe((button: any) => {
      this.button = button;
    });
  }

}



