import {Component, OnInit} from '@angular/core';
import {MessagesService} from '../messages.service';
import {ButtonsService} from '../buttons.service';


@Component({
  selector: 'app-messages-list',
  templateUrl: './messages-list.component.html',
  styleUrls: ['./messages-list.component.css']
})
export class MessagesListComponent implements OnInit {
  allMessages: any[];
  allButtons: any[];
  allFlow: any[];

  constructor(private messages: MessagesService,
              private buttonsService: ButtonsService) {
  }

  ngOnInit() {
    this.getAllFlow();
    this.getAllButtons();
    this.getAllMessages();
  }

  /**
   * getting  all messages from API
   */
  getAllMessages(): void {
    this.messages.getAllQuestions().subscribe((res: any) => {
      this.allMessages = res;
    });
  }

  /**
   * getting all buttons from API
   */
  getAllButtons() {
    this.buttonsService.getAllButtons().subscribe((res: any) => {
      this.allButtons = res;
      console.log(this.allButtons);
    });
  }

  geleteButton(id) {
    this.buttonsService.deleteButton(id).subscribe((data: any) => {
      alert('deleted');
      this.getAllButtons();
    });
  }

  deleteMessage(id) {
    this.messages.deleteMessage(id).subscribe((data: any) => {
      alert('deleted');
      this.getAllMessages();
    });
  }

  getAllFlow() {
    this.messages.getAllFlow().subscribe((res: any) => {
      this.allFlow = res;
    });
  }
}



